@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.burger {
  @apply absolute top-3 right-3;
}

.menu {
  @apply fixed top-0 h-screen bg-gray-700 z-10 text-gray-300 p-6;
  left: -100vw;
  width: 50vw;
  transition: left 200ms linear, width 200ms linear;
}

.menu.open {
  left: 0;
}

.menu.full-size {
  width: 100vw;
}

.menu-top {
  @apply flex items-center justify-start mb-2;
  gap: 8px;
}

.menu-title {
  @apply text-xl text-gray-300
}

.menu-list {
  @apply pl-3 text-gray-400
}

.menu-item:not(:last-child) {
  @apply mb-1;
}

.menu-link {
  @apply relative;
}

.menu-link::before {
  content: '';
  @apply left-0 absolute bottom-[-2px] bg-gray-400;
  height: 1px;
  width: 0;
  transition: width 200ms linear;
}

@media (hover: hover) {
  .menu-link:hover::before {
    @apply w-full;
  }
}

.menu-top button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.menu-close {
  background-color: #FF6157;
  border: 1px solid #E24640;
}

.menu-full-size {
  background-color: rgba(42, 203, 66, 1);
  border: 1px solid rgba(27, 172, 44, 1);
}

.parent-list {
  @apply max-w-screen-xl w-full mx-auto relative;
}

.body-list {
  @apply px-6 pt-9;
}

.parent-title {
  @apply text-2xl mb-5 text-gray-900
}

.parent-title strong {
  @apply text-gray-800;
}

.list {
  @apply mb-5 pl-5
}

.list ul {
  @apply pl-4 py-2
}

.body-list ul li {
  position: relative;
}

.body-list ul li::before {
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 50%;
  position: absolute;
  left: -10px;
  top: 13px;
  @apply bg-gray-600
}

.item {
  @apply text-lg text-gray-600
}

.item strong {
  @apply text-gray-900
}

.end {
  @apply bg-gray-800 px-6 py-6
}

.end .parent-title {
  @apply text-white
}

.end .item {
  @apply text-gray-400 pb-2
}

.footer {
  @apply bg-gray-900 text-white text-center px-6 py-6
}